import React from 'react';
import logo from '../images/logo.svg';
import { useSelector, useDispatch } from 'react-redux';

function Footer() {
  const viewFooter = useSelector(state => state.footer);

  // eslint-disable-next-line
  if (viewFooter == 1) {
    return (
      <div>
        <img alt="" src={logo} style={{ width: 50, height: 50 }}></img>
        <h1
          style={{
            color: '#000000',
            fontSize: 10
          }}
        >
          POWERED BY PRIORITY WEB
        </h1>
      </div>
    );
  } else {
    return null;
  }
}
export default Footer;
